@import url('https://fonts.googleapis.com/css?family=Gugi|Play|Rajdhani|Righteous');
:root{
	--bg-color: rgba(143, 162, 193, .6);
	--border-color: white;
	/* GREEN: rgba(96, 229, 34, .4) */
	/* BLUE/GREY rgba(143, 162, 193, .6) */
}
.container-fluid{
	
	min-height: 1080px;
	
	color: white;
	background-image: url("images/download.jpg");
	text-align: center;
	margin: none;
}
.container-fluid {
	
	width: 100wh;
	min-height: 100vh;
	height: auto;
	color: #fff;
	background: linear-gradient(-45deg, #d5ba23, #95dd21, #23A6D5, #23D5AB);
	background-size: 400% 400%;
	-webkit-animation: Gradient 20s ease infinite;
	-moz-animation: Gradient 20s ease infinite;
	animation: Gradient 20s ease infinite;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@-moz-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
.spacer10px{
	min-height: 10px;
}
.gitlink:hover{
	cursor: pointer;
}
.gitlink:hover .gittext{
	
	text-decoration: underline;
}
.gitlink:hover .gitimage{
	border-radius: 50%;
	background-color: transparent;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.linkButton:hover {
	background-color: #0066cc;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.showcaseinner{
	font-family: 'Rajdhani', sans-serif;
	background-color: var(--bg-color);
	border-style: dashed;
	
	border-bottom-style: solid;
	border-width: 5px;
	border-color: var(--border-color);
	-webkit-animation: pulseGlow 5s infinite;
	-moz-animation: pulseGlow 5s infinite;
	animation: pulseGlow 5s infinite;
	
	text-align: center;
}
.showcaseLower{
	margin-top: 2vh;
	padding-top: 10px;
	padding-bottom: 10px;
	background-color: var(--bg-color);
	border-style: solid;

	border-width: 5px;
	border-color: var(--border-color);
	-webkit-animation: pulseGlow 5s infinite;
	-moz-animation: pulseGlow 5s infinite;
	animation: pulseGlow 5s infinite;
	
	text-align: center;
}
.showcaseLower ul{
	list-style: none;
}
.resumeView{
	min-width: 100%;
	min-height: 800px;
}

.navigation{
	background-color: var(--bg-color) ;
	/* background-image: linear-gradient(rgba(168, 68, 255, .4),rgba(85, 67, 102, .4)); */
	border-style: solid;
	border-width: 5px;
	border-color: var(--border-color);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
	
}
.navBar{
}
.navBar li{
	padding: 5px;
	display: inline;
	
}
.leftBar{
	display: table;
	min-height: 100%;
	padding: 5px;
	/*background-color: var(--bg-color);*/
	border-style: solid;
	border-width: 5px;
	border-color: var(--border-color);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
}
.leftBar li{
	padding: 5px;
	display: inline;
}
.leftButton{
	color: white;
	background-color: rgba(0,0,0,.5);
	/*background-color: rgba(224, 236, 255, .5);*/
	border: none;
	border-radius: 0px;
	
}

.leftButton:hover{
	background-color: rgba(224, 236, 255, .5);
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.showcaseButton{
	box-shadow: none;
	color: black;
	background-color: var(--border-color);
	
	border: none;
	border-radius: 0px;
}
.showcaseButton:hover{
	color: white;
	background-color: rgba(224, 236, 255, .5);
	box-shadow: 0 12px 16px 0 rgba(256, 256, 256,0.24), 0 17px 50px 0 rgba(256, 256,256,0.19);
	box-shadow: none;
}
.active{
	background-color: rgba(0, 0, 0, .5);
	color: white;
}
.textLeft{
	border-left-style: solid;
	border-left-width: 5px;
	border-width: 5px;
	border-color: var(--border-color);
	text-align: left;
}
.tabcontent{
	animation: fadeEffect 1s;
}
@keyframes fadeEffect{
	from {opacity: 0;}
	to {opacity: 1;}
}
@keyframes pulseGlow{
	0%, 100% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);}
	50% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.9), 0 17px 50px 0 rgba(256,256,256,0.7);}
}