@import url(https://fonts.googleapis.com/css?family=Gugi|Play|Rajdhani|Righteous);
@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
	--bg-color: rgba(143, 162, 193, .6);
	--border-color: white;
	/* GREEN: rgba(96, 229, 34, .4) */
	/* BLUE/GREY rgba(143, 162, 193, .6) */
}
.container-fluid{
	
	min-height: 1080px;
	
	color: white;
	background-image: url(data:image/jpeg;base64,);
	text-align: center;
	margin: none;
}
.container-fluid {
	
	width: 100wh;
	min-height: 100vh;
	height: auto;
	color: #fff;
	background: -webkit-linear-gradient(135deg, #d5ba23, #95dd21, #23A6D5, #23D5AB);
	background: linear-gradient(-45deg, #d5ba23, #95dd21, #23A6D5, #23D5AB);
	background-size: 400% 400%;
	-webkit-animation: Gradient 20s ease infinite;
	animation: Gradient 20s ease infinite;
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
.spacer10px{
	min-height: 10px;
}
.gitlink:hover{
	cursor: pointer;
}
.gitlink:hover .gittext{
	
	text-decoration: underline;
}
.gitlink:hover .gitimage{
	border-radius: 50%;
	background-color: transparent;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.linkButton:hover {
	background-color: #0066cc;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.showcaseinner{
	font-family: 'Rajdhani', sans-serif;
	background-color: rgba(143, 162, 193, .6);
	background-color: var(--bg-color);
	border-style: dashed;
	
	border-bottom-style: solid;
	border-width: 5px;
	border-color: white;
	border-color: var(--border-color);
	-webkit-animation: pulseGlow 5s infinite;
	animation: pulseGlow 5s infinite;
	
	text-align: center;
}
.showcaseLower{
	margin-top: 2vh;
	padding-top: 10px;
	padding-bottom: 10px;
	background-color: rgba(143, 162, 193, .6);
	background-color: var(--bg-color);
	border-style: solid;

	border-width: 5px;
	border-color: white;
	border-color: var(--border-color);
	-webkit-animation: pulseGlow 5s infinite;
	animation: pulseGlow 5s infinite;
	
	text-align: center;
}
.showcaseLower ul{
	list-style: none;
}
.resumeView{
	min-width: 100%;
	min-height: 800px;
}

.navigation{
	background-color: rgba(143, 162, 193, .6);
	background-color: var(--bg-color) ;
	/* background-image: linear-gradient(rgba(168, 68, 255, .4),rgba(85, 67, 102, .4)); */
	border-style: solid;
	border-width: 5px;
	border-color: white;
	border-color: var(--border-color);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
	
}
.navBar{
}
.navBar li{
	padding: 5px;
	display: inline;
	
}
.leftBar{
	display: table;
	min-height: 100%;
	padding: 5px;
	/*background-color: var(--bg-color);*/
	border-style: solid;
	border-width: 5px;
	border-color: white;
	border-color: var(--border-color);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
}
.leftBar li{
	padding: 5px;
	display: inline;
}
.leftButton{
	color: white;
	background-color: rgba(0,0,0,.5);
	/*background-color: rgba(224, 236, 255, .5);*/
	border: none;
	border-radius: 0px;
	
}

.leftButton:hover{
	background-color: rgba(224, 236, 255, .5);
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.showcaseButton{
	box-shadow: none;
	color: black;
	background-color: white;
	background-color: var(--border-color);
	
	border: none;
	border-radius: 0px;
}
.showcaseButton:hover{
	color: white;
	background-color: rgba(224, 236, 255, .5);
	box-shadow: 0 12px 16px 0 rgba(256, 256, 256,0.24), 0 17px 50px 0 rgba(256, 256,256,0.19);
	box-shadow: none;
}
.active{
	background-color: rgba(0, 0, 0, .5);
	color: white;
}
.textLeft{
	border-left-style: solid;
	border-left-width: 5px;
	border-width: 5px;
	border-color: white;
	border-color: var(--border-color);
	text-align: left;
}
.tabcontent{
	-webkit-animation: fadeEffect 1s;
	        animation: fadeEffect 1s;
}
@-webkit-keyframes fadeEffect{
	from {opacity: 0;}
	to {opacity: 1;}
}
@keyframes fadeEffect{
	from {opacity: 0;}
	to {opacity: 1;}
}
@-webkit-keyframes pulseGlow{
	0%, 100% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);}
	50% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.9), 0 17px 50px 0 rgba(256,256,256,0.7);}
}
@keyframes pulseGlow{
	0%, 100% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);}
	50% {box-shadow: 0 12px 16px 0 rgba(256,256,256,0.9), 0 17px 50px 0 rgba(256,256,256,0.7);}
}
body {
  font-family: "Open Sans", serif;
  color: white;
}

.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
	height: auto;
	background-color: rgba(150,150,150,.9);
	border-style: solid;
	border-color: rgb(79, 142, 242);
	border-width: 5px;
	border-radius: 5%;
	opacity: .95;
	box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
	width: 80%;
	margin: 0 auto;
}

.title-container {
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  
}
.Titles{
	border-style: solid;
	border-color: rgb(79, 142, 242);
	border-width: 5px;
	border-radius: 5px;
	padding: 5px;
	background-color: rgba(0, 0, 0, .8);
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.7), 0 17px 50px 0 rgba(0,0,0,0.7);

	
}

.padding{
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	min-height: 10vh;
}
.weather-panel{
	text-align: center;
	min-height: 30vh;
	border-style: solid;
	border-color: rgb(79, 142, 242);
	border-width: 5px;
	border-radius: 5px;	
	padding: 5px;
	background-color: rgba(0, 0, 0, .8);
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.7), 0 17px 50px 0 rgba(0,0,0,0.7);
}
.title-container__title {
  font-size: 100px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #2c3e50;
  height: 70vh;
  padding-top: 100px;
  padding-left: 50px;
}

input[type="text"] {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.7), 0 17px 50px 0 rgba(0,0,0,0.7);
  background-color: white;
  border: 0;
  border-bottom: solid 3px rgb(79, 142, 242);
  border-left: solid 3px rgb(79, 142, 242);
  width: 30%;
  padding-bottom: 4px;
  color: #000 !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}



input:-webkit-autofill {
    -webkit-text-fill-color: #000 !important;
}

button {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.7), 0 17px 50px 0 rgba(0,0,0,0.7);
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: rgb(79, 142, 242);
  color: #fff;
  font-weight: 100;
}
button:hover{
	background-color: #3f910b;
	box-shadow: 0 12px 16px 0 rgba(0,0,0,0.7), 0 17px 50px 0 rgba(0,0,0,0.7);
}

button:active {
  outline: none;
}


.signup-background{
	justify-content: center;
	align-items: center;
	height: 100vh;
	display: flex;
	color: white;
	background-image: url(data:image/jpeg;base64,);
	text-align: center;
	margin: none;
	
}
.signup-form{
	justify-content: center;
	align-items: center;
	position: relative;
	top: -20vh;
	width: 70vh;
	
	color: white;
	
	border-radius: 20px;
	background-color: rgba(96, 229, 34, .4);
	border-style: dashed;
	
	border-width: 5px;
	border-color: rgba(256,256,256,0.5);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
	text-align: center;
}

