.signup-background{
	justify-content: center;
	align-items: center;
	height: 100vh;
	display: flex;
	color: white;
	background-image: url("images/download.jpg");
	text-align: center;
	margin: none;
	
}
.signup-form{
	justify-content: center;
	align-items: center;
	position: relative;
	top: -20vh;
	width: 70vh;
	
	color: white;
	
	border-radius: 20px;
	background-color: rgba(96, 229, 34, .4);
	border-style: dashed;
	
	border-width: 5px;
	border-color: rgba(256,256,256,0.5);
	box-shadow: 0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19);
	text-align: center;
}
